// [ SOCIAL COLORS ]
$brand-colors: () !default;
$brand-colors: map-merge((
        "facebook":   #3b5998,
        "google":     #dd4b39,
        "gplus":      #dd4b39,
        "twitter":    #00aced,
        "linkedin":   #007bb6,
        "pinterest":  #cb2027,
        "git":        #666666,
        "tumblr":     #32506d,
        "vimeo":      #aad450,
        "youtube":    #bb0000,
        "flickr":     #ff0084,
        "reddit":     #ff4500,
        "dribbble":   #ea4c89,
        "skype":      #00aff0,
        "instagram":  #517fa4,
        "lastfm":     #c3000d,
        "soundcloud": #ff8800,
        "behance":    #1769ff,
        "envato":     #82b541,
        "medium":     #00ab6c,
        "spotify":    #1db954,
        "quora":      #a82400,
        "xing":       #026466,
        "snapchat":   #fffc00,
        "telegram":   #0088cc,
        "whatsapp":   #075e54,
        "rss":        #f26522,
), $brand-colors);

$color-accent: $living-coral !default;
$color-accent-inverse: #ffffff !default;
